<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <table class="table mt-4">
      <thead>
        <tr>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in lists" :key="item.id">
          <td>{{ item.create_at }}</td>
          <td>
            <ul class="list-unstyled">
              <li v-for="productitem in item.products" :key="productitem.id">
                {{ productitem.product.title }} : {{ productitem.qty
                }}{{ productitem.product.unit }}
              </li>
            </ul>
          </td>
          <td>{{ item.total | currency }}</td>
          <td v-if="item.is_paid" class="text-success"></td>
          <td v-else class="text-danger"></td>
          <td>
            <button
              class="btn btn-outline-secondary btn-sm"
              @click="openModal(item)"
            >
              
            </button>
            <button
              class="btn btn-outline-primary btn-sm" v-if="!item.is_paid"
              @click="editerModal(item)"
            >
              
            </button>
          </td>
        </tr>
      </tbody>
    </table>
<pagination :pages="pagination" @emit-page="getOrders" />
    <!--  -->
    <div
      class="modal fade"
      id="ordersModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="border rounded m-2">
              <div class="order_title"></div>
              <table class="table order">
                <tbody v-if="tempProduct.user">
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.user.name }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.user.address }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.user.tel }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.user.email }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.message }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="border rounded m-2">
              <div class="order_title"></div>
              <table class="table order">
                <tbody>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.create_at }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.total | currency }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>
                      <span v-if="tempProduct.is_paid" class="text-success"
                        ></span
                      >
                      <span v-else></span>
                    </td>
                  </tr>
                  <tr v-if="tempProduct.is_paid">
                    <th scope="row"></th>
                    <td>{{ tempProduct.paid_date }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="border rounded m-2">
              <div class="product_title"></div>
              <table class="table">
                <thead>
                  <th></th>
                  <th></th>
                  <th></th>
                </thead>
                <tbody>
                  <tr v-for="item in tempProduct.products" :key="item.id">
                    <td class="align-middle">{{ item.product.title }}</td>
                    <td class="align-middle">
                      {{ item.qty }}/{{ item.product.unit }}
                    </td>
                    <td class="align-middle text-right">
                      {{ item.final_total | currency }}
                    </td>
                  </tr>
                </tbody>
                <tfoot>
                  <!-- <tr>
                                <td colspan="2" class="text-right"></td>
                                <td class="text-right" >{{ filtercoupon }}</td>
                            </tr> -->
                  <tr>
                    <td colspan="2" class="text-right"></td>
                    <td class="text-right">
                      {{ tempProduct.total | currency }}
                    </td>
                  </tr>
                </tfoot>
              </table>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-secondary"
              data-dismiss="modal"
            >
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div
      class="modal fade"
      id="editerModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-primary text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="border rounded m-2">
              <div class="order_title"></div>
              <table class="table order">
               <tbody v-if="tempProduct.user">
                <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.user.name }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.user.address }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.user.tel }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.user.email }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.message }}</td>
                  </tr>
                </tbody> 
              </table>
            </div>
            <div class="border rounded m-2">
              <div class="order_title"></div>
              <table class="table order">
                <tbody>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.create_at }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>{{ tempProduct.total | currency }}</td>
                  </tr>
                  <tr>
                    <th scope="row"></th>
                    <td>
                      <span v-if="tempProduct.is_paid" class="text-success"
                        ></span
                      >
                      <span v-else class="text-danger"> <button class="btn btn-warning" @click="payOrder()"></button></span>
                    </td>
                  </tr>
                  <tr v-if="tempProduct.is_paid">
                    <th scope="row"></th>
                    <td>{{ tempProduct.paid_date }}</td>
                  </tr>
                </tbody>
              </table>
            </div>
            <div class="border rounded m-2">
              <div class="product_title"></div>
              <table class="table">
                <thead>
                  <th></th>
                  <th></th>
                  <th></th>
                </thead>
                <tbody>
                  <tr v-for="item in tempProduct.products" :key="item.id">
                    <td class="align-middle">{{ item.product.title }}</td>
                    <td class="align-middle">
                      {{ item.qty }}/{{ item.product.unit }}
                    </td>
                    <td class="align-middle text-right">
                      {{ item.final_total | currency }}
                    </td>
                  </tr>
                </tbody>
                <tfoot>
                  <!-- <tr>
                                <td colspan="2" class="text-right"></td>
                                <td class="text-right" >{{ filtercoupon }}</td>
                            </tr> -->
                  <tr>
                    <td colspan="2" class="text-right"></td>
                    <td class="text-right">
                      {{ tempProduct.total | currency }}
                    </td>
                  </tr>
                </tfoot>
              </table>
            </div>
          </div>
          <div class="modal-footer">
            <button
              type="button"
              class="btn btn-outline-secondary"
              data-dismiss="modal"
            >
              
            </button>
            <!-- <button
              type="button"
              class="btn btn-primary"
              @click="updateOrders()"
            >
              
            </button> -->
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
.order_title,
.product_title {
  color: #9a9ea2;
  padding: 10px;
}
.order {
  th {
    width: 150px;
  }
}
.info {
  display: flex;
  padding: 8px;
  margin-bottom: 12px;
  justify-content: space-between;
  .order_date,
  .order_price,
  .order_financial_status,
  .order_paiddate {
    font-size: 1.2em;
    color: #9a9ea2;
    span {
      color: black;
    }
  }
}
</style>
<script>
import $ from "jquery";
import pagination from '@/components/pagination'

export default {
    components:{
        pagination
    },
  data() {
    return {
      lists: [],
      tempProduct: {},
      isLoading: false,
      pagination:{}
    };
  },
  methods: {
    getOrders(page = 1) {
      const vm = this;
      const api = `${process.env.APIPATH}/api/${process.env.APIID}/admin/orders?page=${page}`;
      (vm.isLoading = true),
        this.$http.get(api).then((response) => {
          console.log(response);
          vm.isLoading = false;
          vm.lists = response.data.orders;
          vm.pagination = response.data.pagination
          
          vm.lists.forEach((item) => {
            const dates1 = new Date(item.create_at * 1000);
            const year = dates1.getFullYear();
            const month = dates1.getMonth() + 1;
            const date = dates1.getDate();
            item.create_at = `${year}/${month}/${date}`;
            const dates2 = new Date(item.paid_date * 1000);
            const year2 = dates2.getFullYear();
            const month2 = dates2.getMonth() + 1;
            const date2 = dates2.getDate();
            item.paid_date = `${year2}/${month2}/${date2}`;
          }); //Unix Timestamp
        });
    },
    payOrder(){
            const vm = this;
            const url = `${process.env.APIPATH}/api/${process.env.APIID}/pay/${vm.tempProduct.id}`
            this.$http.post(url).then((response) => {                
                console.log(response.data);
                this.getOrders();
            });
        },
    openModal(item) {
      this.tempProduct = Object.assign({}, item);
      $("#ordersModal").modal("show");
    },
    editerModal(item) {
      this.tempProduct = Object.assign({}, item);
      $("#editerModal").modal("show");
    },
  },
  created() {
    this.getOrders();
  },
};
</script>


